* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.App {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
  font-family: 'Dosis', sans-serif;
}

.App .input {
  position: fixed;
  top: 0;
  max-width: 1200px;
  width: 100%;
  padding: 20px;
  background-color: rgb(124, 181, 236);
}

::-webkit-input-placeholder {
  color: #888;
}

::-ms-input-placeholder {
  color: #888;
}

::placeholder {
  color: #888;
}

.App .input i {
  color: #888;
  -webkit-transform: translate(-150%, 0);
          transform: translate(-150%, 0);
}

.App .input input {
  cursor: pointer;
  color: #888;
  width: 50%;
  padding: 5px 10px;
  outline: none;
  box-sizing: content-box;
  border-color: transparent;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.highchart {
    margin-bottom: 80px;
}

.highchart p {
    max-width: 1200px;
    position: fixed;
    width: 100%;
    bottom: 0;
    text-transform: uppercase;
    padding: 20px;
    color: white;
    background-color: rgb(124, 181, 236);
}
.weather {
    margin: 110px 0;
}

.weather p {
    font-size: 24px;
    margin-bottom: 40px;
}

.weather .icon {
    margin-bottom: 40px;
    font-size: 80px;
}

.weather h1 span {
    color: rgb(124, 181, 236);
    text-transform: uppercase;
}

.weather i {
    color: rgb(124, 181, 236);
}


.values {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 40px;
}

.values h2 {
    padding: 0 20px;
}

.values h2 i {
    font-size: 40px;
    margin-right: 10px;
    color: rgb(124, 181, 236);
    -webkit-transform: translate(0, 15%);
            transform: translate(0, 15%);

}

@media (max-width: 992px) {
    .values h2 {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .values h2 i {
        margin: 0 0 10px 0;
    }
}

@media (max-width: 768px) {
    .values {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
    }

    .values h2 i {
        margin-bottom: 10px;
    }
}

@media (max-width: 576px) {
    .weather {
        margin: 20px 0;
    }

    .weather .icon,
    .weather p {
        margin-bottom: 20px;
    }

    .values h2 {
        padding: 0 10px;
        margin-bottom: 20px;
    }



}
